<template>
  <div class="post">
    <div v-if="loading" class="loading">
      Loading...
    </div>

    <div v-if="error" class="error">
      {{ error }}
    </div>

    <div v-if="post" class="content">
      <h2>{{ post.title }}</h2>
      <p>{{ post.content }}</p>
    </div>
  </div>
</template>

<script>
import services from '@/http';

export default {
  name: 'Topic',
  data() {
    return {
      loading: false,
      post: null,
      error: null
    };
  },
  created() {
    this.fetchData();
  },
  props: {
    slug: String
  },
  watch: {
    $route: 'fetchData'
  },
  methods: {
    fetchData() {
      this.error = this.post = null;
      this.loading = true;
      //const fetchedId = this.$route.params.id
      console.log('Teste: ', this.slug);

      //this.$http..get(`/items/slug/${this.slug}`)
      services.main
        .findContentBySlug(this.$route.params.pathMatch.substr(1))
        .then(res => {
          const { data } = res;
          this.post = data.item;
        })
        .catch(err => {
          console.log(err.response);
          this.error = err.response.data.error;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },

  beforeRouteUpdate(to, from, next) {
    //this.$refs.topLoader.done();
    console.log('update', to); //not even this
    next();
  },
  beforeRouteLeave(to, from, next) {
    //this.$refs.topLoader.start();
    console.log('leave', to); //not even this
    next();
  }
};
</script>

