<template>
  <div class="post">
    <h1>Topics</h1>

    <div v-if="loading" class="loading">
      Loading...
    </div>

    <div v-if="error" class="error">
      {{ error }}
    </div>

    <div v-if="list.length > 0" class="card-group">
      <router-link
        exact
        :key="item.id"
        tag="div"
        class="card"
        exact-active-class="active"
        :to="{ name: 'topic', params: { slug: item.slug } }"
        v-for="item of list"
      >
        <a href="#">
          {{ item.title }}
        </a>
        {{ item.content }}
      </router-link>
    </div>

    <div v-if="!loading && list.length === 0">
      <p>Nenhum t√≥pico encontrado.</p>
    </div>
  </div>
</template>

<script>
import services from '@/http';

export default {
  name: 'Topics',
  data() {
    return {
      loading: false,
      error: null,
      list: []
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.error = this.post = null;
      this.loading = true;
      //const fetchedId = this.$route.params.id

      //this.$http.get(`/items`)
      services.main
        .findAllContents()
        .then(res => {
          const { data } = res;
          this.list = data.items;
        })
        .catch(err => {
          this.error = err.response ? err.response.data.error : err.message;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>

